.rating {
  // display: inline-grid;
  display: grid;
  grid: auto auto auto / max-content max-content max-content;
  column-gap: 16px;
  grid-row-gap: 4px;
  .thumbs-up {
    grid-column: 1 / 2;
    grid-row: 1 / 2;
  }
  .thumbs-down {
    grid-column: 2 / 3;
    grid-row: 1 / 2;
  }
  .progress {
    grid-column: 1 / span 2;
    grid-row: 2 / 3;

    &.ui.progress:last-child {
      margin-bottom: 0;
    }
  }
  .ui.mini.button {
    background: none;
    &:focus {
      background: none;
    }
  }
}
